* {
  box-sizing: border-box;
}

html,
body,
.App,
#root {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  overflow: hidden;
}

canvas {
  display: none;
}
#root {
  overflow: auto;
}

body {
  position: fixed;
  overflow: hidden;
  overscroll-behavior-y: none;
  font-family: -apple-system, BlinkMacSystemFont, avenir next, avenir,
    helvetica neue, helvetica, ubuntu, roboto, noto, segoe ui, arial, sans-serif;
  color: black;
  -webkit-font-smoothing: antialiased;
  background: floralwhite;
}

.html-wrapper {
  position: absolute;
  top: 100px;
  left: 40%;
  color: lightblue;
  font-size: 22px;
  font-weight: bolder;
}

.html-wrapper--back {
  top: 180px;
  right: 40%;
  left: auto;
  color: pink;
}

.intro-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 300px;
  background-image: url(./skyline.png);
  background-size: cover;
}

.intro-image:after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background: linear-gradient(
    0deg,
    rgba(255, 250, 240, 1) 0%,
    rgba(255, 250, 240, 1) 20%,
    rgba(255, 250, 240, 0) 40%
  );
}
